/* product labels */
.i {
	svg {
		$size: 20px;
		display: block;
		position: absolute;
		left: 50%;
		top: 50%;
		width: $size;
		height: $size;
		margin: -#{round($size/2)} 0 0 -#{round($size/2)};

		path {
			fill: currentColor; } } }

.prod__labels {
	position: relative; }

.prod__label {
	$size: 34px;

	display: block;
	@include size($size);
	border-radius: round($size/2);
	color: white;
	text-indent: -9999px;
	overflow: hidden;
	background-color: $base-color;
	background-repeat: no-repeat;
	background-position: center;
	background-size: 16px auto;

	@include media('<mob-lg') {
		$size: 26px;
		@include size($size);
		border-radius: round($size/2);
		background-size: 14px auto; }

	& ~ .prod__label {
		margin-top: 10px; } }

.prod__label--new {
	background-image: url(../../img/icon-new.svg);
	background-size: 20px auto;

	@include media('<mob-lg') {
		background-size: 18px auto; } }

.prod__label--hit {
	background-image: url(../../img/icon-like.svg); }

.prod__label--share {
	background-image: url(../../img/icon-act.svg); }

.prod__label--sale {
	background-image: url(../../img/icon-sale.svg);
	background-size: 14px auto;

	@include media('<mob-lg') {
		background-size: 12px auto; } }

/* product labels end */

/* product item */
.prod {
	position: relative;
	display: block;
	height: 100%;
	text-decoration: none;
	overflow: hidden;
	color: inherit; }

a.prod__inner {
	display: block;
	height: 100%;
	min-height: 150px;
	padding: nth($products-grid-space, 1) nth($products-grid-space, 2) 80px;
	text-decoration: none;
	color: inherit;

	@include media('<desk-sm') {
		padding: nth($products-grid-space--md, 1) nth($products-grid-space--md, 2) 70px; }

	@include media('<mob-lg') {
		padding: nth($products-grid-space--mob, 1) nth($products-grid-space--mob, 2) 70px; } }

.prod__labels {
	position: absolute;
	left: nth($products-grid-space, 2);
	top: nth($products-grid-space, 2);
	z-index: 1;

	@include media('<desk-sm') {
		left: nth($products-grid-space--md, 2);
		top: nth($products-grid-space--md, 2); }

	@include media('<mob-lg') {
		left: nth($products-grid-space--mob, 2);
		top: nth($products-grid-space--mob, 2); } }

.prod__figure {
	position: relative;
	max-width: 240px; // ограничиваем ширину изображения продукта
	padding: 0;
	margin: 0 auto $bottom-space--element;

	@include media('<desk-sm') {
		margin-bottom: $bottom-space--element--md; }

	@include media('<mob-lg') {
		margin-bottom: $bottom-space--element--mob; }

	&:last-child {
		margin-bottom: 0; }

	&:before {
		content: "";
		display: block;
		@include ratio(1, 1); }

	img {
		display: block;
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		max-width: none;
		@include object-fit(scale-down); } }

.prod__code, .prod__title {
	display: block;
	@include font-normal;
	@include font(18px, round-float(24/18, 2));
	letter-spacing: 0.1em;
	text-align: center;
	max-width: 260px;
	margin: 0 auto;

	@include media('<desk-sm') {
		@include font(16px, round-float(19/16, 2));
		letter-spacing: 0.05em;
		margin-bottom: 5px; } }

.prod__code {
	color: $text-color-light; }

.prod__title {
	@include font-bold;
	margin-bottom: 0.5em;

	&:last-child {
		margin-bottom: 0; } }

// info (show hover)
.prod__info {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	z-index: 5;
	pointer-events: none; }

.prod__body {
	//position: absolute
	//width: 100%
	//left: 50%
	//top: 50%
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	padding: nth($products-grid-space, 1) nth($products-grid-space, 2) 140px;
	//transform: translate(-50%, -50%)

	@include media('<desk-sm') {
		padding: nth($products-grid-space--md, 1) nth($products-grid-space--md, 2) 140px; }

	@include media('<mob-lg') {
		padding: nth($products-grid-space--mob, 1) nth($products-grid-space--mob, 2) 140px; } }

// list of characteristics
.prod__dl-list {
	margin-bottom: $bottom-space--element;
	font-size: 1.8rem;
	line-height: round-float(22/18, 2);
	text-align: center;

	@include media('<desk-sm') {
		margin-bottom: $bottom-space--element--md;
		font-size: 1.6rem;
		line-height: round-float(18/16, 2); }

	@include media('<mob-lg') {
		margin-bottom: $bottom-space--element--mob; }

	&:last-child {
		margin-bottom: 0; } }

.prod__dl-list__di {
	position: relative;
	margin-top: 14px;

	@include media('<desk-sm') {
		margin-top: 6px; } }

.prod__dl-list__dt {
	display: block;
	color: $text-color-light;

	@include media('<desk-sm') {
		font-size: 1.4rem; } }

// footer
.prod__foot {
	position: absolute;
	left: 0;
	bottom: 0;
	width: 100%;
	padding: 0 10px nth($products-grid-space, 1);
	z-index: 5;

	//+media('<desk-sm')
	//adding-bottom: nth($products-grid-space--md, 1)

	@include media('<mob-lg') {
		padding-bottom: nth($products-grid-space--mob, 1);
		bottom: auto;
		top: 100%;
		margin-top: -65px; } }

// price
.prod__price {
	text-align: center;
	line-height: 24px;

	.no {
		@include font-black;
		font-size: 24px;

		@include media('<desk-sm') {
			font-size: 20px; } } }

.prod__price__cur,
.prod__price__old {
	position: relative;
	display: inline-block;
	@include margin-horizontal(7px);

	@include media('<mob-lg') {
		display: block;
		@include margin-horizontal(0); } }

.prod__price__cur__val {
	@include font-black;
	font-size: 24px;

	@include media('<desk-sm') {
		font-size: 20px; } }

.prod__price__old {
	color: $base-color;
	text-decoration: line-through;

	&::before {
		content: "/ ";
		color: $text-color;
		display: block;
		position: absolute;
		right: 100%;
		top: 0;
		margin-right: 5px;

		@include media('<mob-lg') {
			display: none; } } }

.prod__price__old__val {
	@include font-normal; }

// add info
.prod__add-info {
	position: absolute;
	left: 0;
	bottom: 0;
	width: 100%;
	padding: 0 nth($products-grid-space, 2)  nth($products-grid-space, 1);
	z-index: 5;

	//+media('<desk-sm')
	//adding: 0 nth($products-grid-space--md, 2)  nth($products-grid-space--md, 1)

	@include media('<mob-lg') {
		padding: 0 nth($products-grid-space--mob, 2)  30px; } }

// Hover animation. Only no-touch

.prod {
	$animation-speed: 0.53s;
	$animation-elements: ".prod__labels, .prod__figure, .prod__dl-list__di, .prod__code, .prod__title, .prod__foot, .prod__add-info";

	#{$animation-elements} {
		// transition
		$prop: transform, opacity;
		will-change: $prop;
		transition: {
			property: $prop;
			duration: $animation-speed; } }

	.prod__dl-list__di {
		opacity: 0;
		$x: -50px;
		$y: 0;
		transform: translate($x, $y);
		transform: translate3d($x, $y, 0);

		@for $i from 1 through 4 {
			&:nth-child(#{$i + 1}) {
				transition: {
					delay: 0.05s * $i; } } } }

	.prod__foot {
		pointer-events: none; }

	.prod__add-info {
		opacity: 0;
		pointer-events: none;
		$x: 0;
		$y: 50px;
		transform: translate($x, $y);
		transform: translate3d($x, $y, 0); }

	// hover
	.no-touchevents &:hover {
		$animation-speed: 0.35s;
		#{$animation-elements} {
			transition-duration: $animation-speed; }

		.prod__labels {
			opacity: 0; }

		.prod__figure {
			opacity: 0.1;
			$scale: 0.88;
			transform: scale($scale);
			transform: scale3d($scale, $scale, 1); }

		.prod__info {
			pointer-events: auto; }

		.prod__dl-list__di {
			opacity: 1;
			$x: 0;
			$y: 0;
			transform: translate($x, $y);
			transform: translate3d($x, $y, 0); }

		.prod__code,
		.prod__title {
			opacity: 0;
			$x: 0;
			$y: -50px;
			transform: translate($x, $y);
			transform: translate3d($x, $y, 0); }

		.prod__foot {
			$x: 0;
			//$y: -120px
			$y: -70px;
			transform: translate($x, $y);
			transform: translate3d($x, $y, 0); }

		.prod__add-info {
			opacity: 1;
			$x: 0;
			//$y: -20px
			$y: 0;
			transform: translate($x, $y);
			transform: translate3d($x, $y, 0);

			[class*="btn-"] {
				pointer-events: auto; } } } }

/* product item end */

/* products list */
.main-section--products {
	overflow: hidden; }

.products {
	overflow: hidden;
	margin-bottom: $bottom-space--article;

	@include media('<desk-sm') {
		margin-bottom: $bottom-space--article--md; }

	@include media('<mob-lg') {
		margin-bottom: $bottom-space--article--mob; }

	.main-section--products & {
		margin-left: -2px;
		margin-right: -2px; }

	&:last-child {
		margin-bottom: 0; } }

.products__list {
	display: flex;
	flex: 0 1 auto;
	justify-content: stretch;
	flex-flow: row wrap;
	@include clear-spacing; }

.products__item {
	@include grid-item(5, nth($products-space, 2), nth($products-space, 1));
	@include font($font-size, $line-height);
	letter-spacing: $letter-spacing;

	//+media('<desk-sm')
	@include media('<1550px') {
		@include grid-item-responsive(4, nth($products-space, 2), nth($products-space, 1)); }

	//+media('<tab-sm')
	@include media('<1150px') {
		@include grid-item-responsive(3, nth($products-space, 2), nth($products-space, 1)); }

	//+media('<mob-lg')
	@include media('<865px') {
		@include grid-item-responsive(2, nth($products-space, 2), nth($products-space, 1)); }

	// Оставить 5 елементов
	// Остальные скрыть
	.main-section--products & {
		&:nth-child(n + 6) {
			display: none; }

		// Оставить 4 елементов
		// Остальные скрыть
		@include media('<1550px', '>=1150px') {
			&:nth-child(n + 5) {
				display: none; } }

		// Оставить 6 елементов
		// Остальные скрыть
		//+media('<tab-sm', '>=mob-lg')
		@include media('<1150px') {
			&:nth-child(n) {
				display: inline-block; }

			&:nth-child(n + 7) {
				display: none; } }

		// Оставить 8 елементов
		// Остальные скрыть
		//+media('<mob-lg')
		//:nth-child(n + 9)
 } }		//display: none

// дополнительные бордеры нужны для safari
// т.к. в этом браузере не поддерживается автовысота во flex-box элементах
// подходит только, если не нужно делать всю область кликабельной
.products, .products__list, .products__item {
	position: relative;

	&::before, &::after {
		content: "";
		display: block;
		position: absolute;
		border: 0 solid $border-color; } }

.products {
	padding-right: 1px;

	&::before {
		top: 0;
		left: 0;
		@include size(0, 9999px);
		border-left-width: 1px; }

	&::after {
		left: 0;
		bottom: 0;
		@include size(9999px, 0);
		border-bottom-width: 1px; } }

.products__list, .products__item {
	&::before {
		top: 0;
		left: 0;
		@include size(9999px, 0);
		border-bottom-width: 1px; }

	&::after {
		right: -1px;
		top: 0;
		@include size(0, 9999px);
		border-right-width: 1px; } }

// дополнительные бордеры нужны для safari END
/* products list end */

/* main sections */
.main-section {
	padding-top: nth($main-section-space-vertical, 1);
	padding-bottom: nth($main-section-space-vertical, 2);

	@include media('<desk-sm') {
		padding-top: nth($main-section-space-vertical--md, 1);
		padding-bottom: nth($main-section-space-vertical--md, 2); }

	@include media('<mob-lg') {
		padding-top: nth($main-section-space-vertical--mob, 1);
		padding-bottom: nth($main-section-space-vertical--mob, 2); } }

.main-section__heading {
	position: relative;
	margin-bottom: $bottom-space--article;

	@include media('<desk-sm') {
		margin-bottom: $bottom-space--article--md; }

	@include media('<mob-lg') {
		margin-bottom: $bottom-space--article--mob; }

	h1, h2, h3 {
		@include font-normal;
		color: $text-color;

		& {
			@include font-h1; }

		& > span {
			display: inline-block;
			vertical-align: top;
			position: relative;

			&::after {
				@include media('>=desk-sm') {
					content: "";
					display: block;
					position: absolute;
					left: 100%;
					bottom: 10px;
					width: 100px;
					height: 2px;
					background-color: currentColor;
					margin-left: 20px; } } } } }

/* main sections end */

/* main enter */
.main-enter {
	position: relative;
	overflow: hidden;
	margin-bottom: nth($main-section-space-vertical, 2);
	background-color: rgba($img-mask-bg, 0.5);

	@include media('<desk-sm') {
		//margin-bottom: nth($main-section-space-vertical--md, 2)
		margin-bottom: 0; }

	&:last-child {
		margin-bottom: 0; } }

/* main enter end */

/* promo slider */
.main-section--promo {
	height: calc(100vh - #{$header-height} - #{$nav-height});
	min-height: 400px;
	padding-top: 0;
	padding-bottom: 0;

	@include media('<desk-ml') {
		height: calc(100vh - #{$header-height--md} - #{$nav-height}); }

	@include media('<desk-sm') {
		//height: calc(100vh - #{$header-height--sm})
		//height: 575px
		position: relative;
		height: auto;
		min-height: 0; }

	//+media('<tab-lg')
	//eight: 385px

	//+media('<mob-lg')
	//eight: 192px

	&::before {
		content: "";
		display: none;
		@include ratio(192, 82);

		@include media('<desk-sm') {
			display: block; }

		@include media('<mob-lg') {
			display: none; } } }

.promo-slider {
	//width: 600px
	height: 100%;
	//background-color: $bg-color-alt
	color: white;
	border-top: 1px solid $border-color;

	@include media('<desk-sm') {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%; }

	@include media('<mob-lg') {
		position: relative;
		left: auto;
		top: auto;
		width: auto;
		height: auto; }

	// arrows, pagination
	.slider-arrow {
		//top: calc((100% - 25%) / 2)
		//top: round-float(($hundred-percent - 25) / 2, 2)
		top: auto;
		bottom: 140px;
		color: white;
		@include size(nth($slider-promo-button-size, 2), nth($slider-promo-button-size, 1));
		//margin-bottom: -#{round(nth($slider-promo-button-size, 1)/2)}
		margin-bottom: 60px;

		@include media('<desk-sm') {
			//top: calc(100% / 2)
			bottom: auto;
			top: round-float($hundred-percent / 2, 2);
			@include size(nth($slider-promo-button-size--mob, 2), nth($slider-promo-button-size--mob, 1));
			margin-bottom: 0;
			margin-top: -#{round(nth($slider-promo-button-size--mob, 1)/2)}; }

		@include media('<mob-lg') {
			display: none; }

		&::before, &::after {
			content: "";
			display: block;
			position: absolute;
			width: 100%;
			height: 100%;
			left: 0;
			top: 0;
			background-color: $base-color;
			z-index: -1; }

		&::after {
			transform: skew($skew, 0); } }

	.slider-arrow_prev {
		&::after {
			transform-origin: bottom right; } }

	.slider-arrow_next {
		&::after {
			transform-origin: top left; } }

	.swiper-pagination {
		line-height: 0;
		//bottom: 25%
		bottom: 140px;
		margin-bottom: 3rem;

		@include media('<desk-ml') {
			margin-bottom: 2rem; }

		@include media('<desk-sm') {
			bottom: 0;
 }			//margin-bottom: 2rem

		//+media('<desk-sm')
 }		//argin-bottom: $bottom-space--article--mob

	.swiper-pagination-bullet {
		vertical-align: top;
 } }		//color: white

.promo-slider__item {
	display: flex;
	flex-direction: column;
	text-align: center;
	justify-content: center;
	align-items: center;
	position: relative;
	//padding-bottom: $parts-height
	//padding-bottom: 25%
	overflow: hidden;

	@include image-mask;

	@include media('<desk-sm') {
		@include padding-horizontal(nth($slider-promo-button-size--mob, 2) + 50); }

	@include media('<mob-lg') {
		//+padding-horizontal(nth($page-layout-space--mob, 2))
		padding: 0;
		display: block; }

	&::after {
		position: relative;
		content: "";
		display: block;
		width: 100%;
		//height: 25%
		height: 140px;
		pointer-events: none;
		z-index: -1;

		@include media('<desk-sm') {
			display: none; } } }

.promo-slider__item__img {
	position: absolute;
	@include size(100%);
	@include position(0);
	background-color: $bg-color-alt;
	z-index: -2;

	img {
		display: block;
		@include size(100%);
		max-width: none;
		@include object-fit(cover);

		@include media('<mob-lg') {
			height: auto; } }

	&.for-mob {
		display: none; }

	@include media('<mob-lg') {
		& {
			position: relative;
			left: auto;
			top: auto;
			width: auto;
			height: auto;
			display: none;

			&.for-mob {
				display: block; } } } }

.promo-slider__item__content {
	position: relative;
	display: block;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	color: inherit;
	text-decoration: none;
	//width: 960px
	width: 100%;
	max-width: 100%;
	text-align: center;
	flex-grow: 1;

	//+media('<desk-ml')
	//idth: 800px

	//+media('<tab-lg')
	//idth: 480px

	@include media('<mob-lg') {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		@include padding-horizontal(nth($page-layout-space--mob, 2)); } }

.promo-slider__item__title {
	$font-size: 60px;
	$line-height: 70px;
	$font-size--md: 56px;
	$line-height--md: 62px;
	$font-size--sm: 48px;
	$line-height--sm: 58px;
	$font-size--tab: 30px;
	$line-height--tab: 36px;
	$font-size--mob: 18px;
	$line-height--mob: 22px;
	// ===============
	display: block;
	overflow: hidden;
	max-height: round($line-height*3); // maximum 3 line
	margin-left: auto;
	margin-right: auto;
	//margin-bottom: 5rem
	//+font-black
	@include font-semibold;
	@include font($font-size, $line-height);
	letter-spacing: normal;
	text-align: center;
	width: 960px;
	max-width: 100%;

	@include media('<desk-lg') {
		font-size: $font-size--md;
		line-height: $line-height--md;
		max-height: round($line-height--md*3); } // maximum 3 line

	@include media('<desk-ml') {
		font-size: $font-size--sm;
		line-height: $line-height--sm;
		max-height: round($line-height--sm*3); // maximum 3 line
		//margin-bottom: 2rem
		width: 800px; }

	@include media('<tab-lg') {
		font-size: $font-size--tab;
		line-height: $line-height--tab;
		max-height: round($line-height--tab*4); } // maximum 4 line

	@include media('<mob-lg') {
		font-size: $font-size--mob;
		line-height: $line-height--mob;
		max-height: round($line-height--mob*5); // maximum 5 line
		width: 480px; }

	&:last-child {
		margin-bottom: 0; } }

.promo-slider__item__footer {
	position: absolute;
	left: 0;
	//bottom: 9rem
	bottom: 8rem;
	width: 100%;

	@include media('<desk-ml') {
		bottom: 5rem; }

	@include media('<tab-lg') {
		display: none; }

	.more-link {
		@include display-inline-block-top;
		text-decoration: none;
		color: inherit;

		span {
			@include display-inline-block-top;
			line-height: 1.5;
			border-bottom: 2px solid; }

		&:hover {
			color: $base-color;

			span {
				border-color: $base-color; } } }

	.btn-default-alt {
		color: $text-color; } }

.slider-progress {
	display: block;
	position: absolute;
	left: 0;
	//bottom: 25%
	bottom: 0;
	height: 3px;
	width: 100%;
	background-color: rgba(white, 0.25);
	z-index: 5;

	@include media('<desk-sm') {
		display: none; }

	.progress {
		position: absolute;
		left: -100%;
		top: 0;
		width: 100%;
		//width: 0
		height: 100%;
		background-color: $base-color;
		will-change: transform; } }

/* promo slider end */

/* parts */
$width: 25%;

.main-section--parts {
	position: relative;
	padding-top: 0;
	padding-bottom: 0;

	.main-enter & {
		display: flex;
		flex-direction: column;
		justify-content: flex-end;

		@include media('>=desk-sm') {
			position: absolute;
			width: 100%;
			height: 100%;
			left: 0;
			bottom: 0;
			z-index: 5;
			pointer-events: none; } } }

.parts {
	@include media('>=desk-sm') {
		height: 25%;
		height: 140px;
		pointer-events: auto;
		box-shadow: inset 0 1px 0 rgba(white, 0.15);
		background-color: rgba($img-mask-bg, 0.35);
		//background-image: url(../../img/promo-slider-pattern.png)
		//background-size: 25% 100%
		//+linear-gradient(to top, rgba($img-mask-bg, 0.35) 0%, transparent 100%)
 } }		//+linear-gradient(to top, rgba($base-color, 0.35) 0%, transparent 100%)

.parts__list {
	@include media('>=desk-sm') {
		height: 100%; } }

.parts__item {
	float: left;
	//width: calc(100% / 4)
	width: round-float($hundred-percent / 4, 2);

	@include media('>=desk-sm') {
		height: 100%;
		box-shadow: inset 1px 0 0 rgba(white, 0.15); }

	@include media('<desk-sm') {
		position: relative;
		//width: calc(100% / 2)
		width: round-float($hundred-percent / 2, 2);
		z-index: 1; }

	@include media('<mob-md') {
		width: 100%; } }

.parts__item__img {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	z-index: -1;
	opacity: 0;
	pointer-events: none;
	//$y: 20rem
	//$x: 20rem
	//transform: translate(0, $y)
	//transform: translateY($y)
	// transition
	$prop: transform, opacity, visibility;
	will-change: $prop;
	transition: {
		property: $prop;
		duration: $parts-animation-speed-out; }

	@include media('<desk-sm') {
		opacity: 1; }

	@include image-mask;

	img {
		position: relative;
		width: 100%;
		height: 100%;
		max-width: none;
		@include object-fit(cover);
		z-index: -2; } }

.parts__item__thumb {
	position: relative;
	overflow: hidden;
	height: 100%;
	color: white;
	z-index: 10;

	&::before {
		@include media('>=desk-sm') {
			content: "";
			display: block;
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			z-index: -1;
			background-color: $base-color;
			opacity: 0;
			// transition
			$prop: opacity, transform;
			will-change: $prop;
			transition: {
				property: $prop;
				duration: $parts-animation-speed-out;
 } }				//delay: $parts-animation-speed

		@include media('<desk-sm') {
			content: "";
			display: block;
			pointer-events: none;
			@include ratio(435, 186); }

		@include media('<mob-md') {
			padding: 0;
			height: 100px; } }

	.p1, .p2 {
		display: block;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		position: absolute;
		left: 0;
		top: 0;
		height: 100%;
		width: 50%;
		padding: 0 10px;
		z-index: 1;
		//+font-bold
		@include font-semibold;
		color: inherit;
		text-align: center;
		text-decoration: none;
		border: 0 solid rgba(white, 0.25);
		// transition
		$prop: opacity;
		will-change: $prop;
		transition: {
			property: $prop;
			duration: $parts-animation-speed-out; }

		@include media('<desk-sm') {
			width: 100%; }

		.t, .l {
			width: 100%;
			// transition
			$prop: transform;
			will-change: $prop;
			transition: {
				property: $prop;
				duration: $parts-animation-speed-out; } }

		.t {
			font-size: 3rem;
			font-size: 2.8rem;
			line-height: round-float(30/30, 2);
			//letter-spacing: 0.05em
			letter-spacing: normal;
			margin-bottom: 0.3em;

			@include media('<desk-lg') {
				font-size: 2.4rem; }

			@include media('<desk-ml') {
				font-size: 2rem; }

			@include media('<desk-sm') {
				font-size: 2.4rem; } }

		.l {
			opacity: 0.5;
			font-size: 1.8rem;
			line-height: round-float(30/18, 2);
			//letter-spacing: 0.05em
			letter-spacing: normal;

			@include media('<desk-ml') {
				@include font-normal;
				font-size: 1.6rem; }

			@include media('<desk-sm') {
				opacity: 1;
				font-size: 1.8rem; } }

		svg {
			$width: 40px;
			$height: 20px;
			display: block;
			position: absolute;
			bottom: 20px;
			bottom: 12px;
			left: 50%;
			height: $height;
			width: $width;
			margin-left: -#{round-float($width/2, 2)};
			opacity: 0;
			$x: -50%;
			transform: translate($x, 0);
			transform: translateX($x);
			// transition
			$prop: transform, opacity;
			will-change: $prop;
			transition: {
				property: $prop;
				duration: $parts-animation-speed; }

			@include media('<desk-ml') {
				$width: 30px;
				$height: 15px;
				bottom: 10px;
				height: $height;
				width: $width; }

			path, polygon {
				fill: currentColor; } } }

	.p1 {
		@include media('>=desk-sm') {
			left: 0;

			.t, .l {
				$x: 50%;
				transform: translate($x, 0);
				transform: translateX($x); } } }

	.p2 {
		left: 50%;
		border-left-width: 1px;
		opacity: 0;
		pointer-events: none;

		@include media('<desk-sm') {
			display: none; }

		&::before {
			$size: 120px;
			content: "";
			display: block;
			position: absolute;
			width: $size;
			height: $size;
			background: url(../../img/icon-new.svg) no-repeat;
			background-size: contain;
			opacity: 0.2;
			$x: 50%;
			transform: translate($x, 0);
			transform: translateX($x);
			// transition
			$prop: transform;
			will-change: $prop;
			transition: {
				property: $prop;
				duration: $parts-animation-speed-out; } }

		.t, .l {
			$x: 20%;
			transform: translate($x, 0);
			transform: translateX($x); } }

	.p1, .p2 {
		.no-touchevents &:hover {
			//background-color: #343434

			svg {
				opacity: 1;
				$x: 0;
				transform: translate($x, 0);
				transform: translateX($x); } } } }

.parts__item__prods {
	$space: 30px;
	$space--sm: 15px;
	position: absolute;
	overflow: hidden;
	left: 0;
	//bottom: 25%
	//height: 75%
	bottom: 140px;
	height: calc(100% - 140px);
	z-index: 1;
	width: $width;
	pointer-events: none;

	@include media('<desk-sm') {
		display: none; }

	& > ul {
		display: flex;
		flex-direction: column;
		list-style: none;
		margin: 0;
		padding: 0;
		height: 100%;
		background-color: rgba(white, 1);
		opacity: 0;
		$y: 10rem;
		transform: translate(0, $y);
		transform: translateY($y);
		// transition
		$prop: transform, opacity, visibility;
		will-change: $prop;
		transition: {
			property: $prop;
			duration: $parts-animation-speed-out; }

		&::before {
			$color: black;
			content: "";
			display: block;
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 26px;
			@include linear-gradient(to bottom, rgba($color, 0.1) 0%, rgba($color, 0) 100%);
			opacity: 0;
			pointer-events: none;
			z-index: 5;
			// transition
			$prop: opacity;
			will-change: $prop;
			transition: {
				property: $prop;
				duration: $parts-animation-speed-out; } }

		& > li {
			position: relative;
			flex-grow: 1;
			flex-shrink: 1;
			height: 33.33%;
			max-height: 33.33%;

			&::before {
				content: "";
				display: block;
				position: absolute;
				top: 0;
				left: $space;
				right: $space;
				border-top: 1px solid #e6e6e6;

				@include media('<desk-ml') {
					left: $space--sm;
					right: $space--sm; } }

			&:first-child {
				&::before {
					content: none; } }

			& > a {
				display: block;
				position: relative;
				height: 100%;
				padding: $space;
				color: inherit;
				text-decoration: none;

				@media (max-height: 800px) {
					padding: $space--sm $space; }

				@include media('<desk-ml') {
					padding: $space--sm; }

				&:hover {
					ul li:nth-child(2) {
						color: $base-color; } } }

			.prod__labels {
				position: absolute;
				left: $space;
				top: $space;

				@include media('<desk-ml') {
					left: $space--sm;
					top: $space--sm; } }

			img {
				float: left;
				width: 34%;
				max-width: 144px;
				margin-right: $space;

				@include media('<desk-ml') {
					margin-right: $space--sm; } } } }

	ul ul {
		list-style: none;
		margin: 0;
		padding: 20px 0 0;
		overflow: hidden;
		@include font-normal;
		font-size: 1.8rem;
		line-height: 2.4rem;
		color: #000;

		@media (max-height: 800px) {
			padding-top: 10px; }

		@include media('<desk-ml') {
			padding-top: 10px;
			font-size: 1.6rem;
			line-height: 2.0rem; }

		& > li {
			position: relative;
			@include truncate(100%);

			&:first-child {
				@include font-normal;
				color: #b2b2b2; }

			&:last-child {
				margin-top: 15px;
				b {
					@include font-black;
					font-size: 2.4rem; } } } } }

.parts__item {
	&:nth-child(1) {
		.parts__item__prods {
			left: $width*0; } }

	&:nth-child(2) {
		.parts__item__prods {
			left: $width*1; } }

	&:nth-child(3) {
		.parts__item__prods {
			left: $width*2; } }

	&:nth-child(4) {
		.parts__item__prods {
			left: $width*3; } } }

// hover
.main-enter {
	.main-section--promo {
		// transition
		$prop: opacity;
		will-change: $prop;
		transition: {
			property: $prop;
			duration: $animation-speed; } }

	@include media('>=desk-sm') {
		.no-touchevents &:hover {
			.main-section--promo {
				opacity: 0;

				&:hover {
					opacity: 1; } } } } }

.parts__item:hover {
	.parts__item__img {
		opacity: 1;
		transition: {
			duration: $parts-animation-speed; } }

	.parts__item__thumb {
		&:before {
			opacity: 1;
			transition: {
				duration: $parts-animation-speed;
 } }				//delay: 0s

		.p1, .p2 {
			.t, .l {
				$x: 0;
				transform: translate($x, 0);
				transform: translateX($x);
				transition: {
					duration: $parts-animation-speed; } } }

		.p2 {
			opacity: 1;
			pointer-events: auto;

			&::before {
				$x: 0;
				transform: translate($x, 0);
				transform: translateX($x);
				transition: {
					duration: $parts-animation-speed; } } } }

	.parts__item__prods {
		pointer-events: auto;

		& > ul {
			opacity: 1;
			$y: 0;
			transform: translate(0, $y);
			transform: translateY($y);
			transition: {
				duration: $parts-animation-speed;
 }				//delay: $parts-animation-speed

			&::before {
				opacity: 1; } } } }

/* parts end */

/* collections */
.collection-slider {
	.swiper-container {
		border: none;
		margin-left: 0;
		margin-right: 0; } }

.main-section--collections {
	overflow: hidden;

	@include media('<desk-sm') {
		background-color: $bg-color; } }

.collection-slider__item {
	position: relative;
	overflow: hidden;
	display: block;
