		margin: -$bottom-space--article -#{nth($grid-space, 2)} $bottom-space--element 0;

		@include media('<desk-sm') {
			margin: -$bottom-space--article--md -#{nth($grid-space--md, 2)} $bottom-space--element--md 0; }

		@include media('<mob-lg') {
			margin: -$bottom-space--article--mob -#{nth($grid-space--mob, 2)} $bottom-space--element--mob 0; } }

	&:last-child {
		margin-bottom: 0; }

	a {
		text-decoration: none;
		color: inherit;
		// transition
		$prop: color;
		transition: {
			property: $prop;
			duration: $animation-speed; }

		&:hover {
			color: $base-color; } }

	& > li {
		//width: calc(100% / 4)
		width: round-float($hundred-percent / 4, 2);
		display: inline-block;
		vertical-align: top;
		@include font(1.8rem, 2.4rem);
		letter-spacing: $letter-spacing;
		padding-right: nth($grid-space, 2);
		margin-top: 4rem;

		@include media('<desk-md') {
			//width: calc(100% / 3)
			padding-right: 20px;
 }			//width: round-float($hundred-percent / 3, 2)

		@include media('<desk-sm') {
			margin-top: 3rem;
			padding-right: nth($grid-space--md, 2); }

		@include media('<mob-lg') {
			margin-top: 2rem;
			padding-right: nth($grid-space--mob, 2); }

		& > a {
			display: inline-block;
			vertical-align: top;
			@include font-bold;
			@include font(2.4rem, round-float(30/24, 2));

			& + ul {
				margin-top: 2.5rem;

				@include media('<desk-sm') {
					margin-top: 1.5rem; }

				//+media('<mob-lg')
 } }				//argin-top: $bottom-space--element--mob

		ul {
			& > li {
				margin-top: 1rem;

				a {
					padding-top: 0.5em; }

				ul {
					display: none; }

				li {
					font-size: 0.85em;
					padding-left: 1em; } } } } }

/* footer navigation end */

/* footer social share */
.soc-share {
	overflow: hidden;
	line-height: 0;
	margin-bottom: $bottom-space;
	@include clear-spacing;

	&:last-child {
		margin-bottom: 0; } }

.soc-share__list {
	position: relative; }

.soc-share__item {
	margin: 2px 0; }

.soc-share__item__label,
.soc-share__item__list {
	display: inline-block;
	vertical-align: top;
	@include font($font-size, 20px);
	letter-spacing: normal; }

.soc-share__item__label {
	margin: 0 10px 10px 0;

	@include media('<tab-lg') {
		margin: 0 7px 7px 0; } }

.soc-share__item__list {
	@include clear;

	& > a {
		display: block;
		float: left;
		margin: 0 10px 10px 0;
		position: relative;
		color: inherit;
		transition: {
			property: color, border-color;
			duration: $animation-speed; }

		@include media('<tab-lg') {
			margin: 0 7px 7px 0; }

		span {
			@include hide-text; }

		svg {
			display: block;
			@include size(20px); }

		path {
			fill: currentColor; }

		&:hover {
			color: $base-color; } } }

/* footer social share end */

/* developer */
.developer {
	display: block;

	.footer .footer-col &, & {
		margin-bottom: 0;

		@include media('>=mob-lg') {
			width: auto;
			padding: 0; } }

	@include media('<mob-lg') {
		font-size: 14px;
		line-height: 30px;
		border-top: 1px solid $border-color;
		//padding-top: $bottom-space--row
		padding-top: 2rem; }

	a {
		text-decoration: none;

		.no-touchevents &:hover {
			text-decoration: none; } } }

.developer__label {
	display: inline-block;
	vertical-align: top; }

.developer__logo {
	@include display-inline-block-top;
	position: relative;
	top: 5px;
	padding-right: 3px;
	@include size(154px, 20px);
	text-align: left;
	text-indent: -9999px;

	&::before, &::after {
		@include pseudo();
		@include position(0, 0);
		@include size(149px, 16px); }

	&::before {
		@include pseudo();
		@include position(4px, 4px);
		background: url('data:image/png;base64,');
		background-size: 2px; }

	//.no-touchevents a:hover
	//ransform: translate(0, 1px)
	//ilter: blur(2px)
	//pacity: 0.6

	&::after {
		@include pseudo();
		background: #00ef85 url('data:image/png;base64,') no-repeat center center;
		background-size: auto 16px;

		.no-touchevents a:hover & {
			transform: translate(0, -1px); } } }

/* developer end */
